import Column from "./Column"
import gorilla from "../images/gorilla.png"
import potemkin from "../images/pot_pray.png"
import glasses from "../images/glasses.jpg"

const Triple = () => {
  return (
    <div className="triple">
        <Column src={gorilla} 
                alt="Gorilla sitting on a small chair"
                heading="Gorilla"
                text="Lorem ipsum dolor sit, amet consectetur adipisicing elit. Pariatur eum exercitationem expedita nesciunt deleniti labore dolores cumque natus alias iste?">
        </Column>
        <Column src={potemkin} 
                alt="Potemkin 6P" 
                heading="Potemkin"
                text="Potemkin's go to anti-air. You'd think that due to his size, he'd have a hard time anti-airing, but this 6P has him sit down and still gives him a bunch of upper body invulnerability. Its great hitbox size smacks a lot of things out of the sky.">
        </Column>
        <Column src={glasses}
                alt="Orang Utan with glasses"
                heading="Orang Utan"
                text="Lorem ipsum dolor sit, amet consectetur adipisicing elit. Pariatur eum exercitationem expedita nesciunt deleniti labore dolores cumque natus alias iste?"></Column>
    </div>
  )
}

export default Triple
